<template>
  <div class="hello">
    <div class="ts container">
      <div class="ts heading horizontally very padded slate">
        <span class="header">選擇網站類別 ＆ 支援平台</span>
        <div class="ts section horizontal divider">&#x2606;&#x2606;&#x2606;</div>
        <div class="ts grid">
          <div class="eight wide column">
            <span class="ts massive sub header">這是一個什麼類型的網站？</span>
            <span class="description">必需</span>
            <div class="ts section hidden divider"></div>
            <div class="ts checkboxes">
              <div class="ts radio checkbox">
                <input type="radio" name="webtype" id="general" @click="choosetype('general')">
                <label for="general">一般常見的網站</label>
              </div>
              <div class="ts hidden divider"></div>
              <div class="ts radio checkbox">
                <input type="radio" name="webtype" id="mobile" @click="choosetype('mobile')">
                <label for="mobile">手機的 App（IOS / Android）</label>
              </div>
              <div class="ts hidden divider"></div>
              <div class="ts radio checkbox">
                <input type="radio" name="webtype" id="article" @click="choosetype('article')">
                <label for="article">文章</label>
              </div>
              <div class="ts hidden divider"></div>
              <div class="ts radio checkbox">
                <input type="radio" name="webtype" id="video" @click="choosetype('video')">
                <label for="video">影片</label>
              </div>
              <div class="ts hidden divider"></div>
              <div class="ts radio checkbox">
                <input type="radio" name="webtype" id="product" @click="choosetype('product')">
                <label for="product">產品</label>
              </div>
            </div>
          </div>
          <div class="eight wide column">
            <span class="ts massive sub header">它支援哪些平台呢？</span>
            <span class="description">選擇</span>
            <div class="ts section hidden divider"></div>

            <div class="ts checkboxes">

              <div class="ts checkbox">
                <input type="checkbox" id="og" @click="chooseplatform('og')">
                <label for="og">Open Graph - Facebook/pinterest/Google+/Linkedln</label>
              </div>

              <div class="ts hidden divider"></div>

              <div class="ts checkbox">
                <input type="checkbox" id="twitter" @click="chooseplatform('twitter')">
                <label for="twitter">Twitter</label>
              </div>

            </div>
          </div>
        </div>
      </div>

      <div class="ts inverted divider"></div>

      <div class="ts heading horizontally very padded slate">
        <!--一般資訊  -->
        <general v-if="general_select"></general>
        <div class="ts section hidden divider"></div>

        <!--Mobile  -->
        <mobile v-if="mobile_select"></mobile>
        <div class="ts section hidden divider" v-if="mobile_select"></div>


        <!--Open Graph  -->
        <opengraph v-if="og_select"></opengraph>
        <div class="ts section hidden divider" v-if="og_select"></div>
        <!--文章  -->
        <articles v-if="article_select && og_select"></articles>
        <div class="ts section hidden divider" v-if="article_select && og_select"></div>
        <!--影片  -->
        <videos v-if="video_select && og_select"></videos>
        <div class="ts section hidden divider" v-if="video_select && og_select"></div>

        <!--產品  -->
        <product v-if="product_select && og_select"></product>
        <div class="ts section hidden divider" v-if="product_select && og_select"></div>

        <!--Twitter  -->
        <twitter v-if="tw_select"></twitter>
        <div class="ts section hidden divider" v-if="tw_select"></div>
        <twittermobile v-if="(mobile_select && tw_select)"></twittermobile>
        <div class="ts section hidden divider" v-if="(mobile_select && tw_select)"></div>
        <twittervideo v-if="(video_select && tw_select)"></twittervideo>
        <div class="ts section hidden divider" v-if="(video_select && tw_select)"></div>

      </div>

      <div class="ts horizontal divider">複製以下並貼上（在 &lt;head>...&lt;/head> 中）</div>

      <div class="ts insetted slate" id="codes">
        <!-- General -->
        <span>&lt;!-- COMMON TAGS --&gt;</span>
        <span>&lt;meta charset="utf-8"&gt;</span>
        <span v-if="$store.state.web_title != '' ">&lt;title&gt;{{ $store.state.web_title }}&lt;/title&gt;</span>
        <span>&lt;!-- Search Engine --&gt;</span>
        <span v-if="$store.state.web_description != '' ">&lt;meta name="description" content="{{ $store.state.web_description }}"&gt;</span>
        <span v-if="$store.state.web_image != '' ">&lt;meta name="image" content="{{ $store.state.web_image }}"&gt;</span>
        <span>&lt;!-- Schema.org for Google --&gt;</span>
        <span v-if="$store.state.web_title != '' ">&lt;meta itemprop="name" content="{{ $store.state.web_title }}"&gt;</span>
        <span v-if="$store.state.web_description != '' ">&lt;meta itemprop="description" content="{{ $store.state.web_description }}"&gt;</span>
        <span v-if="$store.state.web_image != '' ">&lt;meta itemprop="image" content="{{ $store.state.web_image }}"&gt;</span>

        <!-- Mobile -->
        <span v-if="mobile_select">&lt;!-- Mobile App for iOS & Android --&gt;</span>
        <span v-if="(mobile_select && $store.state.ios_app_id != '')">&lt;meta name="apple-itunes-app" content="app-id={{ $store.state.ios_app_id }}"&gt;</span>
        <span v-if="(mobile_select && $store.state.googleplay_id != '')">&lt;meta name="google-play-app" content="app-id={{ $store.state.googleplay_id }}"&gt;</span>
        <!-- Article -->
        <!-- Video -->
        <!-- Product -->

        <!-- Open Graph -->
        <span v-if="og_select">&lt;!-- Open Graph general (Facebook, Pinterest & Google+) --&gt;</span>
        <span v-if="(og_select && $store.state.web_title != '')">&lt;meta name="og:title" content="{{ $store.state.web_title }}"&gt;</span>
        <span v-if="(og_select && $store.state.web_description != '')">&lt;meta name="og:description" content="{{ $store.state.web_description }}"&gt;</span>
        <span v-if="(og_select && $store.state.preview_image != '')">&lt;meta name="og:image" content="{{ $store.state.preview_image }}"&gt;</span>
        <span v-if="(og_select && $store.state.url != '')">&lt;meta name="og:url" content="{{ $store.state.url }}"&gt;</span>
        <span v-if="(og_select && $store.state.site_name != '')">&lt;meta name="og:site_name" content="{{ $store.state.site_name }}"&gt;</span>
        <span v-if="(og_select && $store.state.locale != '')">&lt;meta name="og:locale" content="{{ $store.state.locale }}"&gt;</span>
        <span v-if="(og_select && $store.state.video != '')">&lt;meta name="og:video" content="{{ $store.state.video }}"&gt;</span>
        <span v-if="(og_select && $store.state.admins_id != '')">&lt;meta name="fb:admins" content="{{ $store.state.admins_id }}"&gt;</span>
        <span v-if="(og_select && $store.state.app_id != '')">&lt;meta name="fb:app_id" content="{{ $store.state.app_id }}"&gt;</span>
        <!--type  -->
        <span v-if="(og_select && general_select )">&lt;meta name="og:type" content="website"&gt;</span>
        <span v-if="(og_select && mobile_select )">&lt;meta name="og:type" content="website"&gt;</span>
        <span v-if="(og_select && article_select )">&lt;meta name="og:type" content="article"&gt;</span>
        <span v-if="(og_select && video_select )">&lt;meta name="og:type" content="video.other"&gt;</span>
        <span v-if="(og_select && product_select )">&lt;meta name="og:type" content="product"&gt;</span>
        
        <!-- Open Graph - Mobile App (iOS & Android) -->
        <span v-if="(og_select && mobile_select)">&lt;!-- Open Graph - Mobile App (iOS & Android) --&gt;</span>
        <span v-if="(og_select && mobile_select && $store.state.ios_app_id != '')">&lt;meta name="al:ios:app_store_id" content="{{ $store.state.ios_app_id }}"&gt;</span>
        <span v-if="(og_select && mobile_select && $store.state.ios_app_name != '')">&lt;meta name="al:ios:app_name" content="{{ $store.state.ios_app_name }}"&gt;</span>
        <span v-if="(og_select && mobile_select && $store.state.app_url_1 != '')">&lt;meta name="al:ios:url" content="{{ $store.state.app_url_1 }}"&gt;</span>
        <span v-if="(og_select && mobile_select && $store.state.googleplay_id != '')">&lt;meta name="al:android:package" content="{{ $store.state.googleplay_id }}"&gt;</span>
        <span v-if="(og_select && mobile_select && $store.state.googleplay_name != '')">&lt;meta name="al:android:package" content="{{ $store.state.googleplay_name }}"&gt;</span>
        <span v-if="(og_select && mobile_select && $store.state.app_url_2 != '')">&lt;meta name="al:android:url" content="{{ $store.state.app_url_2 }}"&gt;</span>

        <!-- Open Graph - Article -->
        <span v-if="(og_select && article_select)">&lt;!-- Open Graph - Article --&gt;</span>
        <span v-if="(og_select && article_select && $store.state.section != '')">&lt;meta name="article:section" content="{{ $store.state.section }}"&gt;</span>
        <span v-if="(og_select && article_select && $store.state.publish_time != '')">&lt;meta name="article:published_time" content="{{ $store.state.publish_time }}"&gt;</span>
        <span v-if="(og_select && article_select && $store.state.author != '')">&lt;meta name="article:author" content="{{ $store.state.author }}"&gt;</span>
        <span v-if="(og_select && article_select && $store.state.article_tags != '')">&lt;meta name="article:tag" content="{{ $store.state.article_tags }}"&gt;</span>
        <span v-if="(og_select && article_select && $store.state.expired_time != '')">&lt;meta name="article:expiration_time" content="{{ $store.state.expired_time }}"&gt;</span>
        <span v-if="(og_select && article_select && $store.state.modified_time != '')">&lt;meta name="article:modified_time" content="{{ $store.state.modified_time }}"&gt;</span>

        <!-- Open Graph - Video -->
        <span v-if="(og_select && video_select)">&lt;!-- Open Graph - Video --&gt;</span>
        <span v-if="(og_select && video_select && $store.state.Release_Date != '')">&lt;meta name="video:release_date" content="{{ $store.state.Release_Date }}"&gt;</span>
        <span v-if="(og_select && video_select && $store.state.Video_Height != '')">&lt;meta name="video:height" content="{{ $store.state.Video_Height }}"&gt;</span>
        <span v-if="(og_select && video_select && $store.state.Video_Width != '')">&lt;meta name="video:width" content="{{ $store.state.Video_Width }}"&gt;</span>
        <span v-if="(og_select && video_select && $store.state.Director != '')">&lt;meta name="video:director" content="{{ $store.state.Director }}"&gt;</span>
        <span v-if="(og_select && video_select && $store.state.Writer != '')">&lt;meta name="video:writer" content="{{ $store.state.Writer }}"&gt;</span>
        <span v-if="(og_select && video_select && $store.state.Duration_in_Seconds != '')">&lt;meta name="video:duration" content="{{ $store.state.Duration_in_Seconds }}"&gt;</span>
        <span v-if="(og_select && video_select && $store.state.Actors != '')">&lt;meta name="video:actor" content="{{ $store.state.Actors }}"&gt;</span>
        <span v-if="(og_select && video_select && $store.state.Video_Format != '')">&lt;meta name="video:type" content="{{ $store.state.Video_Format }}"&gt;</span>
        <span v-if="(og_select && video_select && $store.state.Tags != '')">&lt;meta name="video:tag" content="{{ $store.state.Tags }}"&gt;</span>

        <!-- Open Graph - Product (e-commerce) -->
        <span v-if="(og_select && product_select)">&lt;!-- Open Graph - Product (e-commerce) --&gt;</span>
        <span v-if="(og_select && product_select && $store.state.available != '')">&lt;meta name="product:availability" content="{{ $store.state.available }}"&gt;</span>
        <span v-if="(og_select && product_select && $store.state.doller != '')">&lt;meta name="product:price:currency" content="{{ $store.state.doller }}"&gt;</span>
        <span v-if="(og_select && product_select && $store.state.price != '')">&lt;meta name="product:price:amount" content="{{ $store.state.price }}"&gt;</span>
        <span v-if="(og_select && product_select && $store.state.brand != '')">&lt;meta name="product:brand" content="{{ $store.state.brand }}"&gt;</span>

        <!-----------------------------------------------------------------------------------------------------  -->

        <!-- Twitter -->
        <span v-if="tw_select">&lt;!-- Twitter --&gt;</span>
        <!--content  -->
        <span v-if="(tw_select && general_select)">&lt;meta name="twitter:card" content="summary"&gt;</span>
        <span v-if="(tw_select && mobile_select)">&lt;meta name="twitter:card" content="app"&gt;</span>
        <span v-if="(tw_select && article_select)">&lt;meta name="twitter:card" content="summary"&gt;</span>
        <span v-if="(tw_select && video_select)">&lt;meta name="twitter:card" content="player"&gt;</span>
        <span v-if="(tw_select && product_select)">&lt;meta name="twitter:card" content="summary"&gt;</span>
        
        <span v-if="(tw_select && $store.state.web_title != '')">&lt;meta name="twitter:title" content="{{ $store.state.web_title }}"&gt;</span>
        <span v-if="(tw_select && $store.state.web_description != '')">&lt;meta name="twitter:description" content="{{ $store.state.web_description }}&gt;</span>
        <span v-if="(tw_select && $store.state.Publisher_handle != '')">&lt;meta name="twitter:site" content="{{$store.state.Publisher_handle}}"&gt;</span>
        <span v-if="(tw_select && $store.state.Article_author_handle != '')">&lt;meta name="twitter:creator" content="{{$store.state.Article_author_handle}}"&gt;</span>
        <span v-if="(tw_select && $store.state.tw_Preview_Image != '')">&lt;meta name="twitter:image:src" content="{{$store.state.tw_Preview_Image}}"&gt;</span>
        <span v-if="(tw_select && $store.state.Video_Audio_Player_Source != '')">&lt;meta name="twitter:player" content="{{$store.state.Video_Audio_Player_Source}}"&gt;</span>
        
        <!-- Twitter - Mobile App (iOS & Android) -->
        <span v-if="(tw_select && mobile_select)">&lt;!-- Twitter - Mobile App (iOS & Android) --&gt;</span>
        <span v-if="(tw_select && mobile_select && $store.state.ios_app_id != '')">&lt;meta name="twitter:app:id:iphone" content="{{ $store.state.ios_app_id }}"&gt;</span>
        <span v-if="(tw_select && mobile_select && $store.state.ios_app_name != '')">&lt;meta name="twitter:app:name:iphone" content="{{ $store.state.ios_app_name }}"&gt;</span>
        <span v-if="(tw_select && mobile_select && $store.state.app_url_1 != '')">&lt;meta name="twitter:app:url:iphone" content="{{ $store.state.app_url_1 }}"&gt;</span>        
        <span v-if="(tw_select && mobile_select && $store.state.googleplay_id != '')">&lt;meta name="twitter:app:id:googleplay" content="{{ $store.state.googleplay_id }}"&gt;</span>
        <span v-if="(tw_select && mobile_select && $store.state.googleplay_name != '')">&lt;meta name="twitter:app:name:googleplay" content="{{ $store.state.googleplay_name }}"&gt;</span>
        <span v-if="(tw_select && mobile_select && $store.state.app_url_2 != '')">&lt;meta name="twitter:app:url:googleplay" content="{{ $store.state.app_url_2 }}"&gt;</span>
        <span v-if="(tw_select && mobile_select && $store.state.tw_country != '')">&lt;meta name="twitter:app:country" content="{{ $store.state.tw_country }}"&gt;</span>

        <!-- Twitter - Article -->
        <span v-if="(tw_select && article_select)">&lt;!-- Twitter - Article --&gt;</span>
        <!-- Twitter - Video -->
        <span v-if="(tw_select && video_select)">&lt;!-- Twitter - Video --&gt;</span>
        <span v-if="(tw_select && video_select && $store.state.Raw_Stream_URL != '')">&lt;meta name="twitter:player:stream:content_type" content="{{ $store.state.Raw_Stream_URL }}"&gt;</span>
        <span v-if="(tw_select && video_select && $store.state.Raw_Stream_URL != '')">&lt;meta name="twitter:player:stream" content="{{ $store.state.Media_Content_Type }}"&gt;</span>
        <span v-if="(tw_select && video_select && $store.state.Raw_Stream_URL != '')">&lt;meta name="twitter:player:width" content="{{ $store.state.Player_Width }}"&gt;</span>
        <span v-if="(tw_select && video_select && $store.state.Raw_Stream_URL != '')">&lt;meta name="twitter:player:height" content="{{ $store.state.Player_Height }}"&gt;</span>

        <!-- Twitter - Product (e-commerce) -->
        <span v-if="(tw_select && product_select)">&lt;!-- Twitter - Product (e-commerce) --&gt;</span>

        <button class="ts right floated inverted button">複製</button>
      </div>

      <div class="ts section hidden divider"></div>

    </div>

    <!--Fix bottom  -->
    <button class="ts pulsing positive right floated button" id="moveDown" @click="jumptocode()">
      產生 mata tags !
    </button>

  </div>
</template>

<script>
import general from './General'
import mobile from './Mobile'
import twittermobile from './Twitter_mobile'
import opengraph from './Open_graph'
import twitter from './Twitter'
import articles from './Article'
import videos from './Video'
import twittervideo from './Twitter_video'
import product from './Product'

export default {
  name: 'hello',
  data() {
    return {
      general_select: true,
      mobile_select: false,
      article_select: false,
      video_select: false,
      product_select: false,
      og_select: false,
      tw_select: false
    }
  },
  components: {
    general,
    mobile,
    twittermobile,
    articles,
    videos,
    twittervideo,
    product,
    opengraph,
    twitter
  },
  methods: {
    jumptocode() {
      document.getElementById('codes').scrollIntoView()
    },
    choosetype(type) {
      this.mobile_select = false
      this.article_select = false
      this.video_select = false
      this.product_select = false

      switch (type) {
        case 'mobile':
          this.mobile_select = true
          break;
        case 'article':
          this.article_select = true
          break;
        case 'video':
          this.video_select = true
          break;
        case 'product':
          this.product_select = true
          break;
        default:
          break;
      }
    },

    chooseplatform(platform) {
      if (platform === 'og') {
        if (document.getElementById('og').checked) this.og_select = true
        else this.og_select = false
      }
      else if (platform === 'twitter') {
        if (document.getElementById('twitter').checked) this.tw_select = true
        else this.tw_select = false
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#moveDown {
  bottom: 20px;
  right: 20px;
  position: fixed;
  z-index: 999;
}

span {
  display: block;
}
</style>



