<script>
export default {
	methods: {
		opennav(idnum) {
			if ((document.getElementById('1').classList.contains('active')) && document.getElementById('n1').classList.contains('active')) {
				document.getElementById('1').classList.remove('active')
				document.getElementById('n1').classList.remove('active')
			}
			if ((document.getElementById('2').classList.contains('active')) && document.getElementById('n2').classList.contains('active')) {
				document.getElementById('2').classList.remove('active')
				document.getElementById('n2').classList.remove('active')
			}
			if ((document.getElementById('3').classList.contains('active')) && document.getElementById('n3').classList.contains('active')) {
				document.getElementById('3').classList.remove('active')
				document.getElementById('n3').classList.remove('active')
			}
			if ((document.getElementById('4').classList.contains('active')) && document.getElementById('n4').classList.contains('active')) {
				document.getElementById('4').classList.remove('active')
				document.getElementById('n4').classList.remove('active')
			}
			if (!document.getElementById(idnum).classList.contains('active'))
				document.getElementById(idnum).classList.add('active')
			document.getElementById('n' + idnum).classList.add('active')
		}
	}
}
</script>




